nav{
    background: rgb(255,111,0);
    background: linear-gradient(180deg, rgba(255,111,0,1) 0%, rgba(250,241,105,1) 85%); 
    height: 60px;
}

.refeer{
    color: white;
    cursor: pointer;
    transition: 100ms;
    width: 200px;
}

.refeer:hover{
    color: #9d00ff;
    scale: 1.05;
    cursor: pointer;
}